import AlertTemplateService from '@/services/commons/AlertTemplateService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let AlertTemplateTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultAlertTemplateTableFilter: {
      type: String,
      default: null
    },
    defaultAlertTemplateTableOrderBy: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      alertTemplateTableData: [],
      alertTemplateTableFilterTimer: null,
      alertTemplateTableFilter: this.defaultAlertTemplateTableFilter,
      alertTemplateTableOrderBy: this.defaultAlertTemplateTableOrderBy,
      alertTemplateTableContainer: 'loading-container',
      alertTemplateTableBusy: false,
      alertTemplateTableTotalRows: 0,
      alertTemplateTableCurrentPage: 1,
      alertTemplateTablePerPage: 10
    }
  },
  computed: {
    alertTemplateTableNoDataText: function () {
      return this.alertTemplateTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    alertTemplateTableTotalPages: function () {
      return Math.ceil(this.alertTemplateTableTotalRows / this.alertTemplateTablePerPage)
    },
    alertTemplateTablePagingInfo: function () {
      return this.pagingInfo(this.alertTemplateTableCurrentPage, this.alertTemplateTablePerPage, this.alertTemplateTableTotalRows)
    }
  },
  methods: {
    loadAlertTemplateTableData (loadingContainerId) {
      let self = this
      this.alertTemplateTableBusy = true
      this.alertTemplateTableContainer = loadingContainerId || this.alertTemplateTableContainer
      this.startLoading(this.alertTemplateTableContainer)
      AlertTemplateService.alertTemplates({
        pageIndex: this.alertTemplateTableCurrentPage - 1,
        pageSize: this.alertTemplateTablePerPage,
        filter: this.alertTemplateTableFilter,
        orderByField: this.alertTemplateTableOrderBy
      }).then(function (response) {
        self.stopLoading(self.alertTemplateTableContainer)
        self.alertTemplateTableBusy = false
        self.alertTemplateTableTotalRows = response.data.totalRows
        self.alertTemplateTableData = response.data.alertTemplateList || []
      })
    },
    refreshAlertTemplateTable () {
      this.loadAlertTemplateTableData()
    },
    filterAlertTemplateTable (filter) {
      let self = this
      if (this.alertTemplateTableFilter !== filter) {
        clearTimeout(this.alertTemplateTableFilterTimer)
        this.alertTemplateTableFilterTimer = setTimeout(() => {
          self.alertTemplateTableFilter = filter
          self.loadAlertTemplateTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.alertTemplateTableFilterTimer)
      }
    },
    orderAlertTemplateTable (orderBy, active) {
      this.alertTemplateTableOrderBy = (active ? '' : '-') + orderBy
      this.loadAlertTemplateTableData()
    }
  },
  watch: {
    alertTemplateTableCurrentPage: function () {
      this.refreshAlertTemplateTable()
    },
    alertTemplateTablePerPage: function () {
      this.refreshAlertTemplateTable()
    }
  }
}
export default AlertTemplateTableMixin
